.redChip {
    background: #ffadad;
    color: #d60000;
    border-color: #d60000;
}

.greenChip {
    background: #b9f8b9;
    color: #006400;
    border-color: #006400;
}

.blueChip {
    background-color: rgb(142, 227, 255);
    color: darkblue;
    border-color: darkblue;
}

.blackChip {
    background-color: rgb(180, 180, 180);
    color: black;
    border-color: black;
}

.addChip {
    background-color: darkgray;
    color: white;
    border-color: black;
}


/* --------- CSS--------- */

.source {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    position: relative;
    margin: -5px 0;
    color: grey;
    font-size: 10px;
    font-weight: 500;
    text-transform: uppercase;
    display: block;
}

.hebrew {
    text-align: right;
}

.speechbubble {
    display: block;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    position: relative;
    padding: 0 2px;
    margin-top: -5px;
}

.narrator {
    display: none;
}

.red {
    color: #d60000;
}

.green {
    color: green;
}

.blue {
    color: #007FFF;
}

