table {
    width: 100%;
    border-collapse: collapse;
    border-spacing:0;
    display:table;
    border: 1px solid #000;
    -webkit-box-shadow:0 4px 10px 0 rgba(0,0,0,0.2),0 4px 20px 0 rgba(0,0,0,0.19);
            box-shadow:0 4px 10px 0 rgba(0,0,0,0.2),0 4px 20px 0 rgba(0,0,0,0.19);
}

table tr {
    border: none;
    border-bottom: 1px solid black;
    border-right: 1px solid black;
}

table thead tr {
    background: #f5f5f5;
    font-weight: bold;
}

table td {
    border: 1px solid black;
    border-top: none;
    border-bottom: none;
    border-right: none;
    padding: .5em;
    position: relative;
}

/* for nested tables */
table td > table {
    width: 100%;
}

td > p:first-child {
    margin-top: 0;
}

td > p:last-child {
    margin-bottom: 0;
}

table tr:first-child {
    font-weight: bold;
}

.table-edit-buttons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    margin-top: -5px;
    margin-bottom: 7px;
    padding-top: -5px;
}

.table-buttons {
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 3px 9px;
    border-radius: 20px;
    margin: 0 5px;
    -ms-flex: 0 0 120px;
        flex: 0 0 120px;
    font-weight: 800;
}

