.select-language {
    position: relative;
    width: 80%;
    padding: 10px;
    margin: 0 auto;
    text-align: center;
}

.sections-divider {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    width: 90%;
    margin: 0 auto;
}

.sections-card {
    -ms-flex: 0 0 150px;
        flex: 0 0 150px;
    height: 150px;
    -webkit-box-shadow: 0 3px 10px rgba(0,0,0,0.16), 0 4px 6px rgba(0,0,0,0.1);
            box-shadow: 0 3px 10px rgba(0,0,0,0.16), 0 4px 6px rgba(0,0,0,0.1);
    font-weight: bold;
    margin: 10px;
    padding-top: 2px;
}

.purpleCard {
    background-color: rgb(227, 218, 252);
    text-decoration: none;
    font-style: normal;
    border: 0.5px solid purple;
}

.redCard {
    background-color: rgb(252, 196, 196);
    text-decoration: none;
    font-style: normal;
    border: 0.5px solid red;
}

.greenCard {
    background-color: rgb(164, 231, 175);
    text-decoration: none;
    font-style: normal;
    border: 0.5px solid green;
}

.blueCard {
    background-color: rgb(186, 203, 250);
    text-decoration: none;
    font-style: normal;
    border: 0.5px solid blue;
}

.otherCard {
    /* background-color: rgb(186, 203, 250); */
    text-decoration: none;
    font-style: normal;
    border: 0.5px solid black;
}

.sections-card-icon {
    position: relative;
    width: 80px;
    height: 80px;
    padding: 1px;
    margin: 0 auto;
}

.sections-card-title {
    position: relative;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: center;
    font-size: 16px;
    text-transform: uppercase;
}

.sections-card-progress-bar {
    position: relative;
    width: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    display: -ms-flexbox;
    display: flex;
}

.current-progress {
    margin-top: 0px;
    font-size: 12px;
}

.loading {
    margin-left: 45%;
}


